.home {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 0 auto;
}
.home-child {
	margin: 0 auto;
	width: 100%;
}
.img1 {
	grid-area: head;
}
.img2 {
	grid-area: body;
}
.img3 {
	grid-area: body2;
}
.img4 {
	grid-area: footer;
}
.description {
	background-color: gray;
}
#img-doors {
	height: 100%;
	width: 100%;
	object-fit: cover;
	/* width: 30em; */
	/* box-shadow: 0px 0px 10px black, 0 0 10px; */
}

.contect-home {
	background-color: gray;
	width: 100%;
	padding: 1%;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	font-size: 0.7em;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
}

#phone-a {
	display: inline;
	color: white;
	text-decoration: none;
	background-color: rgb(75, 69, 69);
	padding: 0.1em 0.3em 0.1em 0.3em;
	border-radius: 10px;
}
@media only screen and (min-width: 600px) {
	.home-child {
		margin: 0 auto;
	}
}
@media only screen and (min-width: 700px) {
	#doors {
		height: 25em;
	}
}
@media only screen and (min-width: 900px) {
	#doors {
		height: 25em;
		background-position: center;
	}
}
@media only screen and (min-width: 1180px) {
	.home {
		margin: 0 auto;
	}
	#doors {
		height: 25em;
		background-position: 50% 40%;
	}
}
