.Products {
	width: 50%;
	margin: 0 auto;
	/* background-image: url("../../images/door.png");
	background-size: cover;
	background-size: 100%;
	background-repeat: no-repeat;
	background-position: center; */
}
.product {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin: 0 auto;
	justify-content: center;
}
.product-child {
	/* width: 80%; */
	margin: 5% 0% 5% 0%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.name {
	/* position: static; */
	float: right;
	/* color: white; */
	font-size: 1em;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	text-decoration: underline 1px;
	/* z-index: 100; */
}
.img {
	width: 20em;
	height: 25em;
	margin: 0 auto;
	margin: 2% 0% 6% 0%;
	align-items: center;
}
#img {
	object-fit: contain;
	width: 70%;
	height: 100%;
}
#right {
	width: 3em;
}
#left {
	width: 3em;
}
@media only screen and (max-width: 800px) {
	.img {
		width: 15em;
		height: 21em;
	}
	.Products {
		width: 100%;
	}
}
@media only screen and (min-width: 600px) {
	.img {
		width: 15em;
		height: 20em;
	}
	/* .product {
		margin: 9% 0% 15% 0%;
	} */
}
@media only screen and (min-width: 600px) {
	.img {
		width: 15em;
		height: 22em;
	}
}
