.logo {
	line-height: 1;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
nav {
	display: flex;
	flex-direction: row;
	width: 100%;
	z-index: 1;
	justify-content: center;
}
.NavLink-item {
	text-decoration: none;
	color: rgb(12, 12, 12);
	font-weight: bold;
	font-family: "Times New Roman", Times, serif;
	font-style: italic;
	border-bottom: 1px solid black;
	width: 15%;
	height: 2em;
	padding-top: 0.5em;
	margin: 0em 0.5em 0.5em 0.5em;
}
.NavLink-item:hover {
	border-bottom: 2px solid black;
}
.NavLink-item:active {
	color: black;
}
.div-menu {
	position: absolute;
	top: 12%;
	left: 0;
	background-color: white;
	width: 50%;
}
.nav-menu {
	display: flex;
	flex-direction: column;
	width: 100%;
	border-right: 1px solid black;
}
.NavLink-item-menu {
	color: black;
	text-decoration: none;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	font-weight: bold;
	padding: 1vh;
	border-bottom: 1px solid black;
}
.NavLink-item-menu:focus {
	background-color: gray;
	color: white;
}
@media only screen and (max-width: 500px) {
	.nav {
		display: none;
	}
	.header {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}
	.top {
		width: 50%;
	}
}
@media only screen and (min-width: 500px) {
	.menu {
		display: none;
	}
}
